
import {useState, useEffect} from "react";
import { Layout, Button } from 'antd';
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
} from '@ant-design/icons';
import Sidebar from '../components/Sidebar';
import Typed from 'react-typed';

function Home() {
 
  const { Header, Sider, Content } = Layout;
  const [openSider, setOpenSider] = useState(true);
  const [city, setCity] = useState("Chandigarh");
  const [myCity, setMyCity] = useState("");
  const [myName, setMyName] = useState("");
  const [myReason, setMyReason] = useState("");
  const [myDate, setMyDate] = useState("")
      return (
        <Layout>
          
          <Sider trigger={null} collapsible collapsed={openSider}>
            <Sidebar />
          </Sider>
          <Layout className="site-layout">
            <Header className="site-layout-background" style={{ padding: 0 }}>
          
           <Button onClick={ ()=>{
                setOpenSider(!openSider);
              }}> < MenuFoldOutlined/></Button>
            </Header>
            <Content
              className="site-layout-background"
              style={{
                margin: '24px 16px',
                padding: 24,
                minHeight: 280,
              }}
              
            >
             
Dear sir,


My name is {myName}. I am from {myCity}. I am student of 12th class. 

{myReason}<br />

I shall be very thankful. Please grant me leave on {myDate}.<br />

Regards
Michale
         <input type="text" name ="My Name" placeholder="My Name" onChange={(e) =>{
           setMyName(e.target.value);
         }}/>
         <input type="text" name ="My City" placeholder="My City" onChange={(e)=>{
           setMyCity(e.target.value);
         }}/>
         <textarea type="text" name ="Reason" placeholder="Reason" onChange={(e)=>{
           setMyReason(e.target.value);
         }}/>
         <input type="date" name ="Date" placeholder="Date" onChange={(e)=>{
           setMyDate(e.target.value);
         }}/>
           {/**         {myCity} <br/>
            <input type="text" name= "Mycity" onChange={ (e)=>{
             setMyCity(e.target.value);
            }} /> **/} 
            </Content>
          </Layout>
        </Layout>
      );
    };
 



export default Home;
